<template>
    <div class="OrderWrap">
        <header>
            <router-link to="/storedetails">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div>确认订单</div>
        </header>
        <div class="adder" @click="site">
            <i class="el-icon-location-outline" ></i>
            <span>请添加一个收货地址</span>
            <i class="el-icon-arrow-right"></i>
        </div>
        <div class="time">
            <span style="font-size: .8rem;
    color: #333;
    font-weight: 700;
    padding-left: .3rem;">送达时间</span>
            <div class="time-right">
                <p>尽快送达 | 预计 12:42</p>
                <span>蜂鸟专送</span>
            </div>
        </div>
        <div class="fangshi">
            <ul>
                <li @click="choose_payment">
                    <span>支付方式</span>
                    <span  @click="" style="font-size: .6rem;color: #aaa;">在线支付</span>
                    <i class="el-icon-arrow-right"></i>
                </li>
                <li>
                    <span style="font-size: .6rem;color: #aaa;"> 红包</span>
                    <span style="color: #aaa;">暂时只在饿了么APP中支持</span>
                </li>
            </ul>

        </div>
        <div class="orderinfo">
            <ul>
                <li>
                    <img :src="'https://elm.cangdu.org/img/'+this.shopinfo.image_path" alt="" class="orderimg">
                    <!--<img src="https://elm.cangdu.org/img/170dc76197f65002.jpg" alt="">-->
                    <span>{{this.shopinfo.name}}</span>
                </li>
                <li v-for="(item , index) in orderArr" :key="item._id">
                    <span>{{item.pro.name}}</span>
                    <span style="color:red;">x{{item.count}}</span>
                    <span>￥{{item.pro.specfoods[0].price}}</span>
                </li>
                <li>
                    <span>餐盒</span>
                    <span>￥23</span>

                </li>
                <li>
                    <span>配送</span>
                    <span>￥4</span>
                </li>
                <li>
                    <span>订单￥{{total+23+4}}</span>
                    <span style="color: red;">待支付￥{{total+23+4}}</span>
                </li>
            </ul>
        </div>
        <div class="foot">
            <p>待支付￥{{total+23+4}}</p>
            <p>确认下单</p>
        </div>
        <payment :pay_choose="showmask" ref="fo"></payment>
    </div>
</template>

<script>
    export default {
        name: "order",
        data(){
            return{
                orderArr:[],
                pid:'',
                shopinfo:{},
                showmask:false,
                shop:[]
            }
        },
        created(){
            this.shop = JSON.parse(localStorage.getItem("shop"));
            this.orderArr = this.$route.query.proArr;
            console.log(this.orderArr);
            this.pid = this.$route.query.pid;
            this.axios.get("shopping/restaurant/"+this.shop.id).then(res=>{
                this.shopinfo = res;
            }).catch(err=>{});
        },
        computed:{
            total(){
                let total_price = 0;
                for (let i = 0; i < this.orderArr.length; i++){
                    let price = 0;
                    price = this.orderArr[i].count*this.orderArr[i].pro.specfoods[0].price;
                    total_price = total_price + price;
                };
                return total_price;
            }
        },
        methods:{
            choose_payment(){
                this.showmask = !this.showmask;
                console.log("这是订单页"+this.showmask)
                // this.$refs.fo.choose();
            },
            site(){
                console.log()
                this.$router.push({path:"/address"}).catch(err=>{})
            }
        }
    };
</script>

